<div class="flex flex-wrap"><div class="button-group mr-8"><div class="bold px-1">Sort by Name</div><button :class="getActiveButton(SortType.NAME_ASC)" @click="setActiveSortType(SortType.NAME_ASC)">&#9650;</button><button :class="getActiveButton(SortType.NAME_DES)" @click="setActiveSortType(SortType.NAME_DES)">&#9660;</button></div><div class="button-group mr-8"><div class="bold px-1">Sort by Active Currency</div><button :class="getActiveButton(SortType.VALUE_ASC)" @click="setActiveSortType(SortType.VALUE_ASC)">&#9650;</button><button :class="getActiveButton(SortType.VALUE_DES)" @click="setActiveSortType(SortType.VALUE_DES)">&#9660;</button></div><div class="button-group"><button class="font-bold" @click="setActiveSortType(SortType.NONE)">Reset</button></div></div>